.app {
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 60px);
        position: relative;
        top: 60px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width : none; // for firefox
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 60px);
            scroll-snap-align: start;
        }
    }
}

/*---Add-Particles---*/
.tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}